<template>
  <main class="content container">
    <div class="content__top">
      <bread-crumbs
        :list="[
          {name: 'main', title: 'Каталог'},
          {name: 'cart', title: 'Корзина'},
          {title: 'Оформление заказа'}]"/>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderInfo?.id }}</span>
      </h1>
    </div>

    <div v-show="$store.state.cartLoading">Загрузка заказа</div>
    <div v-show="$store.state.cartLoadingFailed">Произошла ошибка при загрузке...
      <button @click="loadOrderInfo($route.params.id)">повторить попытку</button>
    </div>

    <section class="cart" v-show="cartLoadingOk">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo?.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo?.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo?.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo?.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderInfo?.basket.items || []" :key="item.product.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.quantity }} x {{ item.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ countProducts }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BreadCrumbs from '@/components/BreadCrumbs.vue';
import { mapActions, mapGetters } from 'vuex';
import numberFormat from '@/helpers/number_format';

export default {
  name: 'OrderInfoPage',
  components: { BreadCrumbs },
  filters: { numberFormat },
  computed: {
    ...mapGetters({
      cartLoadingOk: 'loadingOK',
      orderInfo: 'orderInfo',
    }),
    countProducts() {
      return this.orderInfo?.basket.items.reduce((value, item) => value + item.quantity, 0);
    },
    totalPrice() {
      if (!this.orderInfo?.basket) {
        return 0;
      }
      return this.orderInfo?.basket.items.reduce((value, item) => item.quantity * item.price + value, 0);
    },
  },
  methods: {
    ...mapActions(['loadCart', 'loadOrderInfo']),
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>

