<template>
  <main class="content container">
    <div class="content__top">

      <bread-crumbs
        :list="[
          {name: 'main', title: 'Каталог'},
          {name: 'cart', title: 'Корзина'},
          {title: 'Оформление заказа'}]"/>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ countProducts }} товара
      </span>
    </div>

    <div class="" v-show="formLoading">Обновление данных</div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order" :style="[formLoading ? styleObject: {}]">
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text title="ФИО" placeholder="Введите ваше полное имя" :error="formError.name" v-model="formData.name"/>
            <base-form-text title="Адрес доставки" placeholder="Введите ваш адрес" :error="formError.address" v-model="formData.address"/>
            <base-form-text title="Телефон" placeholder="Введите ваш телефон" :error="formError.phone" v-model="formData.phone"/>
            <base-form-text title="Email" placeholder="Введите ваш email" :error="formError.email" v-model="formData.email"/>

            <base-form-textarea placeholder="Ваши пожелания" :error="formError.comment" v-model="formData.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="delivery_0">
                  <input class="options__radio sr-only" type="radio" id="delivery_0"
                         v-model="formData.delivery" name="delivery" value="0"
                         checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="delivery_500">
                  <input class="options__radio sr-only" v-model="formData.delivery"
                         id="delivery_500" type="radio"
                         name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>
            <span class="form__error" v-if="formError.delivery">{{ formError.delivery }}</span>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="pay_card">
                  <input class="options__radio sr-only" v-model="formData.pay" type="radio"
                         name="pay" value="card" id="pay_card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="pay_cash">
                  <input class="options__radio sr-only" v-model="formData.pay" type="radio"
                         name="pay" value="cash" id="pay_cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
            <span class="form__error" v-if="formError.pay">{{ formError.pay }}</span>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.amount }} x {{ item.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ countProducts }}</b> товара на сумму <b>{{ totalPrice | numberFormat }}₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="formLoading">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-show="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import numberFormat from '@/helpers/number_format';
import { mapGetters } from 'vuex';
import BreadCrumbs from '@/components/BreadCrumbs.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  name: 'OrderPage',
  components: {
    BaseFormText,
    BaseFormTextarea,
    BreadCrumbs,
  },
  filters: { numberFormat },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formLoading: false,
      formLoadingFailed: false,
      styleObject: {
        filter: 'blur(1px)',
      },
    };
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      cartLoadingOk: 'loadingOK',
    }),
    countProducts() {
      return this.products.reduce((value, item) => value + item.amount, 0);
    },
  },
  methods: {
    order() {
      this.formLoading = true;
      this.formLoadingFailed = false;

      this.formError = {};
      this.formErrorMessage = '';
      axios.post(`${API_BASE_URL}/api/orders`, { ...this.formData }, { params: { userAccessKey: this.$store.state.userAccessKey } })
        .then((response) => {
          this.$store.commit('resetCart');
          this.formLoading = false;
          this.formLoadingFailed = false;
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({
            name: 'orderInfo',
            params: { id: response.data.id },
          });
        })
        .catch((error) => {
          this.formLoading = false;
          this.formLoadingFailed = true;
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
  },
};
</script>

