<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image.file.url" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info product__info--color" style="display: none"
       v-if="item.product.colors && item.product.colors.length > 0">
      Цвет: <span><i :style="styleObject"></i></span>
    </p>
    <span class="product__code">Артикул: {{ item.product.id }}</span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click="subAmount"
              :disabled="amount <= 1">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label for="amount">
        <input type="text" id="amount" v-model.number="amount" name="count">
      </label>
      <button type="button" aria-label="Добавить один товар" @click="amount++">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button"
            aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/number_format';
import { mapActions } from 'vuex';

export default {
  name: 'CartItem',
  props: ['item'],
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct' }),
    subAmount() {
      if (this.amount > 1) {
        this.amount -= 1;
      }
    },
  },
  computed: {
    styleObject() {
      if (this.item.product.colors && this.item.product.colors.length > 0) {
        return {
          backgroundColor: this.item.product.colors[0].color,
        };
      }
      return {};
    },
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
};
</script>

