<template>
  <main class="content container">
    <div class="content__top">
      <bread-crumbs
        :list="[
          {name: 'main', title: 'Каталог'},
          {name: 'main', title: category.title},
          {title: product.title}]"/>
    </div>

    <div v-show="productLoading">Загрузка товаров</div>
    <div v-show="productLoadingFailed">Произошла ошибка при загрузке товаров...
      <button @click="loadProduct">повторить попытку</button>
    </div>

    <section class="item" v-show="!productLoading && !productLoadingFailed">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product?.image?.file?.url" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }} ₽
            </b>

            <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click="subAmount"
                        :disabled="productAmount <= 1">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>
                <label for="productAmount">
                  <input type="text" id="productAmount" v-model.number="productAmount">
                </label>

                <button type="button" aria-label="Добавить один товар" @click="productAmount++">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" :disabled="!loadingOK">
                В корзину
              </button>
            </div>
            <div v-show="!loadingOK">Идет отправка данных</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>

          <a href="#">
            Все характеристики
          </a>

          <h3>Что это?</h3>

          <p>
            Описание
          </p>

          <h3>Дизайн</h3>

          <p>
            Описание дизайна
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/number_format';
import BreadCrumbs from '@/components/BreadCrumbs.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'ProductPage',
  components: {
    BreadCrumbs,
  },
  filters: {
    numberFormat,
  },
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
    };
  },
  computed: {
    ...mapGetters(['loadingOK']),
    product() {
      return this.productData || [];
    },
    category() {
      return this.productData?.category || [];
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    subAmount() {
      if (this.productAmount > 1) {
        this.productAmount -= 1;
      }
    },
    addToCart() {
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((resp) => {
          this.productData = resp.data;
        })
        .catch(() => {
          this.productLoadingFailed = true;
        })
        .then(() => {
          this.productLoading = false;
        });
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

