import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    cartLoading: false,
    cartLoadingFailed: false,
    cartProductAdded: false,
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProduct(state, {
      productId,
      amount,
    }) {
      const item = state.cartProducts.find((el) => el.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
        product: item.product,
      }));
    },
    beginLoading(state) {
      state.cartLoading = true;
      state.cartLoadingFailed = false;
    },
    endLoading(state) {
      state.cartLoading = false;
      state.cartLoadingFailed = false;
    },
    errorLoading(state) {
      state.cartLoading = false;
      state.cartLoadingFailed = true;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state, getters) {
      return getters
        .cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    loadingOK(state) {
      return !state.cartLoading && !state.cartLoadingFailed;
    },
    orderInfo(state) {
      return state.orderInfo;
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      context.commit('beginLoading');
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((response) => {
          context.commit('endLoading');
          context.commit('updateOrderInfo', response.data);
        })
        .catch(() => {
          context.commit('errorLoading');
        });
    },
    loadCart(context) {
      context.commit('beginLoading');
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((resp) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', resp.data.user.accessKey);
            context.commit('updateUserAccessKey', resp.data.user.accessKey);
          }
          context.commit('updateCartProductData', resp.data.items);
          context.commit('syncCartProducts');
        })
        .then(() => {
          context.commit('endLoading');
        })
        .catch(() => {
          context.commit('errorLoading');
        });
    },
    addProductToCart(context, {
      productId,
      amount,
    }) {
      context.commit('beginLoading');

      return axios.post(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        quantity: amount,
      }, { params: { userAccessKey: context.state.userAccessKey } })
        .then((resp) => {
          context.commit('updateCartProductData', resp.data.items);
          context.commit('syncCartProducts');
        })
        .then(() => {
          context.commit('endLoading');
        })
        .catch(() => {
          context.commit('errorLoading');
        });
    },
    updateCartProductAmount(context, {
      productId,
      amount,
    }) {
      context.commit('updateCartProduct', {
        productId,
        amount,
      });
      if (amount < 1) {
        return axios.put(`${API_BASE_URL}/api/baskets/products`)
          .catch(() => {

          });
      }
      return axios.put(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        quantity: amount,
      }, { params: { userAccessKey: context.state.userAccessKey } })
        .then((resp) => {
          context.commit('updateCartProductData', resp.data.items);
          context.commit('syncCartProducts');
        })
        .then(() => {
          context.commit('endLoading');
        })
        .catch(() => {
          context.commit('syncCartProducts');
          context.commit('errorLoading');
        });
    },
    deleteCartProduct(context, productId) {
      context.commit('beginLoading');
      return axios.delete(
        `${API_BASE_URL}/api/baskets/products`,
        {
          params: { userAccessKey: context.state.userAccessKey },
          data: { productId },
        },
      )
        .then((resp) => {
          context.commit('updateCartProductData', resp.data.items);
          context.commit('syncCartProducts');
        })
        .then(() => {
          context.commit('endLoading');
        })
        .catch(() => {
          context.commit('errorLoading');
        });
    },
  },
});
