<template>
  <main class="content container">
    <div v-show="$store.state.cartLoading">Загрузка товаров</div>
    <div v-show="$store.state.cartLoadingFailed">Произошла ошибка при загрузке товаров...
      <button @click="loadCart">повторить попытку</button>
    </div>
    <div class="content__top" v-show="cartLoadingOk">
      <bread-crumbs
        :list="[
          {name: 'main', title: 'Каталог'},
          {title: 'Корзина'}]"/>
      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>
    <section class="cart" v-show="cartLoadingOk">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <cart-item v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link :tag="'button'" :to="{name:'order'}" v-show="products.length > 0" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/number_format';
import { mapActions, mapGetters } from 'vuex';
import BreadCrumbs from '@/components/BreadCrumbs.vue';
import CartItem from '@/components/CartItem.vue';

export default {
  name: 'CartPage',
  components: {
    BreadCrumbs,
    CartItem,
  },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      cartLoadingOk: 'loadingOK',
    }),
  },
  methods: {
    ...mapActions(['loadCart']),
  },
};
</script>

